.bannerContainer {
  position: relative;
  background-color: #000;
  width: 100%;
  height: 1000px;
  padding: 80px 5%;
}

.bannerContent {
  position: relative;
  max-width: 1240px;
  margin: auto;
  z-index: 100;
  margin-top: 100px;
}

.bannerBg1,
.bannerBg2 {
  position: absolute;
  top: 0px;
  left: 0;
  width: 100%;
  background-size: cover !important;
  background-repeat: repeat-x !important;
}

.bannerBg1 {
  height: 950px;
  background: url("../../assets/image/banner/bg1.png");
  z-index: 10;
}


.bannerSlogan {
  font-weight: 700;
  font-size: 60px;
  line-height: 96px;
  text-transform: uppercase;
  color: white;
}

.bannerButtomText {
  font-family: 'Outfit';
  font-style: normal;
  font-weight: 400;
  font-size: 48px;
  color: white;
}

.bannerText {
  margin-top: 30px;
  font-size: 20px;
  line-height: 32px;
  color: #ffffff;
}

.bannerEmbraceText {
  margin-top: 260px;
}

.species {
  height: 52px;
  width: 52px;
  display: inline-block;
  vertical-align: sub;
  margin-left: 16px;
  background: url("../../assets/image/banner/species.png");
  z-index: 100;
}

.bannerButton {
  width: 238px;
  height: 60px;
  border: 1.5px solid #FFFFFF;
  border-radius: 80px;
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 60px;
  margin: 50px 0;
  text-align: center;
  cursor: pointer;
  color: #FFFFFF;
  transition: .2s;
}

.bannerButton:hover {
  background: #67d4fd;
  color: #fff;
}

@media screen and (max-width: 768px) {
  .bannerSlogan {
    font-size: 30px;
    line-height: 56px;
  }

  .bannerText {
    font-size: 16px;
    line-height: 24px;
  }
}
